let that;
import {
	updateCo,
	searchCompany
} from '@/axios/index.js'
export default {
	data() {
		return {
			companyName: '',
			companyMark: '',
			companyId: '',
			imgurl: '',
		}
	},
	onLoad() {
		that = this
		that.getCompany()
	},
	methods: {
		getCompany: function() {
			searchCompany().then(res=>{
				console.log(res)
				if(res.ret == 0) {
					that.companyInfo = res.info.list[0]
					that.companyName = that.companyInfo.name
					that.companyMark = that.companyInfo.mark
					that.companyId = that.companyInfo.id
					that.imgurl = that.companyInfo.imgurl
				}
			})
		},
		upCompany: function() {
			var url = ''
			if(that.imgurl != that.companyInfo.imgurl) {
				url = that.imgurl
			} 
			updateCo(url,{
				name: that.companyName,
				mark: that.companyMark,
				id: that.companyId
			}).then(res=>{
				if(JSON.parse(res).ret == 0) {
					uni.$emit('getCompany')
					uni.showToast({
					    title: '更改成功',
					    duration: 2000,
						mask: true,
						success: () => {
							setTimeout(()=>{
								that.$Router.back(1)
							},1500)
						}
					});
				} else {
					console.log(res)
					that.imgurl = that.companyInfo.imgurl 
					uni.showToast({
					    title: '更改失败，请重试',
					    duration: 2000
					});
				}
			})
		},
		upLogo: function() {
			uni.chooseImage({
			    count: 1, 
			    sizeType: ['original', 'compressed'],
			    sourceType: ['album'],
			    success: function (res) {
			        console.log(res.tempFilePaths[0]);
					that.imgurl = res.tempFilePaths[0]
			    }
			});
		},
	}
}
